<template>
  <div class="view-point">
    <van-nav-bar
      class="page__header"
      :title="title"
      left-text="返回"
      left-arrow
      @click-left="$router.go(-1)"
    />
    <div class="page__body">
      <ul class="d-list">
        <li class="d-item" v-for="(item, index) in list" :key="index" @click="switchItem(item.itemInfo.id)">
          <img
            class="logo"
            src="https://zb.oschina.net/file/get?path=static/project-reward/cover/youxikaifa_2.png"
            alt
          />
          <div class="d-item__hd">
            <div class="title">{{ item.itemInfo.title }}</div>
            <div class="desc">{{ item.itemInfo.description }}</div>
          </div>
        </li>
      </ul>
    </div>
  </div>
</template>

<script>
import ynowApi from '../../api/ynow';

export default {
  data() {
    return {
      list: [],
      loading: false
    };
  },
  computed: {
    typeId() {
      return this.$route.query.typeId || 1;
    },
    objectId() {
      return this.$route.query.objectId || 2;
    },
    title() {
      return +this.typeId === 1 ? '我的收藏' : '我的点赞';
    },
    cancelText() {
      return +this.typeId === 1 ? '取消收藏' : '取消点赞';
    }
  },
  mounted() {
    this.doGetCollect();
  },
  methods: {
    switchPage(url) {
      this.$router.push(url);
    },

    switchItem(id) {
      this.$router.push(`/project/${id}`);
    },

    // 获取收藏列表
    async doGetCollect() {
      let res = await ynowApi.getCollect({
        typeId: this.typeId,
        objectId: this.objectId
      });
      if (!res) return;
      if (!res.success) {
        return this.$toast(res.errMsg);
      }
      this.list = res.data.list;
    },

    // 取消收藏
    async doRemoveCollect(record) {
      let res = await ynowApi.removeCollect({
        recordId: record.id
      });
      if (!res) return;
      if (!res.success) {
        return this.$toast(res.errMsg);
      }
      this.doGetCollect();
    }
  }
};
</script>

<style lang="scss" scoped>
.page__body {
  min-height: calc(100vh - 46px);
  background: #fff;
}

.d-list {
  padding: 12px;
  padding-right: 0;
}

.desc {
  font-size: 12px;
  margin-top: 4px;
  color: #999;
}

.logo {
  width: 36px;
  height: 32px;
  margin-right: 12px;
}

.d-item {
  display: flex;
  align-items: center;
}

.d-item__hd {
  flex: 1;
  padding: 12px 0;
  border-bottom: 1px solid #eaeaea;
}
</style>



